.footer {
   background: $navyBlue;
   padding: 50px 0;
       .contact_us{
        @include media('>=desktop'){
            display: flex;
            flex-flow: row wrap;
        }
        .info{
            @include media('>=desktop'){
                width: 50%;
            }
            @include media('>=large-desktop'){
           
            }

                h3{
                    font-family: "Avenir - Medium";
                    font-size: 24px;
                    font-weight: 500;
                    line-height: 34px;
                    color: white;
                    @include media('>=desktop'){
                        font-size: 25px;
                    }
                    @include media('>=large-desktop'){
                        font-size: 30px;
                        line-height: 40px;
                    }
                }
            
              
            .our_contacts {
                display: flex;
                flex-flow: column wrap;
                @include media('>=desktop'){
                    
                }
                @include media('>=large-desktop'){
                    
                }  
                address{
                    font-family: "Avenir - Book";
                    font-size: 14px;
                    font-weight: 300;
                    line-height: 24px;
                    font-style: normal;
                    color: white;
                    margin-bottom: 17px;

                    @include media('>=large-desktop'){
                        font-size: 20px;
                        font-weight: 300;
                        line-height: 30px;
                    }
                }
                .phone{
                    font-size: 14px;
                    font-weight: 300;
                    line-height: 24px;
                    color: white;

                    @include media('>=large-desktop'){
                        font-size: 20px;
                        font-weight: 300;
                        line-height: 30px;
                    }   
                }
                .email{
                    font-size: 14px;
                    font-weight: 300;
                    line-height: 24px;
                    color: white;
                    @include media('>=large-desktop'){
                        font-size: 20px;
                        font-weight: 300;
                        line-height: 30px;
                    }
                }
                
            }      
        
       }
       .join_us{
        padding-top: 10px;
        color: white;
        font-size: 16px;
        font-weight: 300;
        line-height: 26px;
        font-family:$avenir;
    @include media('>=desktop'){
        width: 50%;
    }
    @include media('>=large-desktop'){
        font-size: 25px;
            font-weight: 300;
            line-height: 35px; 
    }
    .button{
        width: 160px;
        height: 50px;
        border: none;
        background: $azure;
        position: relative;
        margin-top: 4px;
        transition: all .5s ease-in-out;
        &:hover{
        background: rgb(69, 73, 62);
        }
        @include media('>=large-desktop'){
            height: 60px;
            margin-top: 18px;
        }
        a{
            
            span{
            font-family: $avenir;
            font-size: 16px;
            font-weight: 500;
            letter-spacing: 1px;
            line-height: 20px;
            text-transform: uppercase;
            color: white;
            padding: 0px;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-right: -50%;
            transform: translate(-50%, -50%);
            
            @include media('>=desktop'){
                top: 54%;
                left: 51%;
            }
            @include media('>=large-desktop'){
                font-size: 18px;
                font-weight: 500;
                letter-spacing: 1px;
                line-height: 22px;
                top:48%;
            }
            }
        }
    }
    }
}
       .membership{
           @include media('>=desktop'){
            margin-top: 60px;
            padding-top: 60px;
           }
           @include media('>=large-desktop'){
            margin-top: 0;
           }

           .cols{
               @include media('>=desktop'){
               display: flex;
               flex-flow: row wrap;
               align-items: center;
               justify-content: space-between;
               }
            .left_column{
                padding-top: 40px;
                @include media('>=desktop'){
                    width: 30%;
                    padding-top: 0;
                }

                p{
                 font-family: "Avenir - Book";
                 font-size: 14px;
                 font-weight: 300;
                 line-height: 24px;
                 color: white;
                 margin-bottom: 15px;
                 @include media('>=desktop'){
                     margin-bottom: 20px;
                 }
                 a{
                     text-decoration: underline;
                     color: white;
                     padding-left: 6px;
                     word-spacing: 3px;
                     
                 }

                }
            }
            .right_column{
                padding-top: 15px;
                padding-bottom: 15px;
                font-family: $avenir;
                 font-size: 14px;
                 font-weight: 300;
                 line-height: 24px;
                 color: white;
                @include media('>=desktop'){
                    width: 70%;
                 }
            }
           }
       }
}

[class^="col"] > * {
    background: transparent;
}