.icon_list{
    margin-top: -60px;
    @include media('>=desktop'){
        margin-top: -190px;
    }
    @include media('>=desktop'){
        margin-top: -307px;
    }
    .list{
        padding: 40px 15px 15px 15px;
        background: white;
        position: relative;
        margin: 0px auto;
        ul{
            @include media('>=tablet'){
                column-count: 2;
             }
             @include media('>=large-desktop'){
                column-count: 3;
            }
            li{
                margin-bottom: 40px;

                &:before{
                    display: none;
                }
                i{
                    
                    position: absolute;
                    margin-left: -15px;
                    z-index: 1;
                    &:before{
                        left: 25px;
                        content: "";
                        background: #eff8ff;
                        border-radius: 15px;
                        width: 30px;
                        height: 30px;
                        opacity: 1;
                        position: absolute;
                        z-index: -1;
                    
                    }
                    @include media('>=large-desktop'){
                    img{
                        background-repeat: no-repeat;
                        margin-left: 5px;
                            max-height: 50px;
                            max-width: 50px;
                    }
                }
               
                
                
            }
            a{
                font-family: $avenir;
                font-size: 18px;
                font-weight: 300;
                display: block;
                line-height: 28px;
                color: #292a2b;
                margin-left: 55px;
                @include media('>=large-desktop'){
                font-size: 25px;
                font-weight: 300;
                line-height: 35px;
               }
            }
        }
    }
}
}