


.main {
  flex-shrink: 0;
  flex-grow: 1;
  // overflow: hidden;
}

.wrapper {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  @include flex(column);
  flex-shrink: 0;
  flex-grow: 1;
  min-height: 100vh;
  height: 100%;
}

.header,
.footer {
  flex-shrink: 0;
}

.container{
    padding: 0 15px;
    margin: 0px auto;
    @include media('>=tablet'){
        max-width: 750px;
    }
     @include media('>=desktop'){
        max-width: 1050px;
      }
      @include media('>=large-desktop'){
        max-width: 1600px;
      }
  }

.section_one{
  @include media('>=large-desktop'){
    margin: 0px auto;
    max-width: 2000px;
  }
}