.hero_section{
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
            .hero_info{
                color: white;
                font-family: $avenir;
                font-size: 18px;
                font-weight: 300;
                margin-top: 50px;
                line-height: 28px;
                max-width: 500px;
                width: 100%;
                @include media('>=desktop'){
                    font-size: 20px;
                    line-height: 31px;
                    min-width: 600px;     
                    font-size: 20px;
                    font-weight: 300;
                    line-height: 30px;
                    max-width: 800px;
                }
                @include media('>=large-desktop'){
                    font-size: 25px;
                    font-weight: 300;
                    line-height: 35px;
                    min-width: 800px;
                    max-width: 1000px;
                }
                h2{
                    font-family: $avenir;
                    font-size: 36px;
                    font-weight: 500;
                    line-height: 46px;
                    word-spacing: 4px;
                    margin-bottom: 22px;
                    @include media('>=desktop'){
                        font-size: 50px;
                        font-weight: 500;
                        line-height: 60px;
                    }
                    @include media('>=large-desktop'){
                        font-size: 85px;
                        font-weight: 500;
                        line-height: 95px;
                        margin-bottom: 30px;
                    }
                }
                p{
                    margin-bottom: 12px;
                    @include media('>=large-desktop'){ 
                        margin-bottom: 25px;
                    }
                }
            }

}