.why_IAA{
    background: $cloud;
    padding: 50px 0;

    .why_we{
            h1{
                font-family: "Avenir - Medium";
                font-size: 32px;
                font-weight: 500;
                line-height: 42px;
                color: $navyBlue;
            @include media('>=desktop'){
                font-size: 40px;
                font-weight: 500;
                line-height: 50px;
            }
            @include media('>=large-desktop'){
                font-size: 60px;
                font-weight: 500;
                line-height: 70px;}
            }
            
            a{
                font-family: "Avenir - Medium";
                font-size: 16px;
                font-weight: 500;
                letter-spacing: 1px;
                line-height: 20px;
                margin-top: 33px;
                text-transform: uppercase;
                color: $azure;
                span{
                    content: "";
                    background-image: url(/assets/images/Rectangle.png);
                    background-repeat: no-repeat;
                    float: left;
                    width: 24px;
                    height: 16px;
                    margin-top: 5px;
                    padding-left: 35px;
                    @include media('>=desktop'){
                        margin-top: 3px;
                    }
                }
            }
        }
        .why_we_achievements{
            padding-top: 35px;
            .achievement{
                padding-top: 24px;
                font-family: $avenir;
                font-size: 16px;
                font-weight: 300;
                line-height: 26px;
                color: $dark-gray;
                border-bottom: 3px solid $dark-gray;
                @include media('>=desktop'){
                    min-height: 100px;
                    height: 100%;
                    margin-bottom: 30px;
                }
                @include media('>=large-desktop'){
                    padding-top: 25px;
                    font-size: 20px;
                font-weight: 300;
                line-height: 30px;
                width: 100%;
                }

                a{
                    @include media('>=desktop'){
                        min-width: 260px;
                        width: 100%;
                        margin-top: 0;
                    }
                    @include media('>=large-desktop'){
                        font-size: 25px;
                        font-weight: 500;
                        line-height: 35px;
                        min-width: 405px;
                    }
                &:before{
                    content:"";
                    background-repeat: no-repeat;
                    background-image: url(/assets/images/check_mark.png);
                    width: 34px;
                    height: 30px;
                    position: absolute;
                }
                    h3{
                        font-family: "Avenir - Medium";
                        font-size: 20px;
                        font-weight: 500;
                        line-height: 30px;
                        color: $navyBlue;
                        margin-left: 50px;
                        margin-bottom: 15px;
                    }
                   
                }
                p{
                    margin-bottom: 26px;
                }
                hr{
                    opacity: 0.25;
                    background: $blue;
                    height: 1px;
                    @include media('>=desktop'){
                        margin: 0px;
                    }
                }
            }
        }
}