.items{
    background: $cloud;
    padding-top: 30px 0;
    @include media('>=desktop') {
        margin: -150px auto;
        width: 100%;
    }
    @include media('>=large-desktop'){
        margin-top: -165px;
    }
    .container{
        .list_items{
            display: flex;
            flex-flow: column wrap;
            align-items: center;
            @include media('>=desktop') {
                flex-flow: row wrap;
                justify-content: space-between;
            }
        .item{
                margin-bottom: 30px;
                position: relative;
                border-radius: 3px;
                max-width: 290px;
                background: white;
                font-family: "Avenir - Book";
                font-size: 16px;
                font-weight: 300;
                line-height: 26px;
                @include media('>=desktop'){
                    min-width: 324px;
                }
                @include media('>=large-desktop'){
                    min-width: 490px;
                }
            .item_content{
                font-family: "Avenir - Book";
                font-size: 16px;
                font-weight: 300;
                line-height: 26px;
                color: $darkGrey;   
                padding: 20px;
            .icon{
                z-index: 10;
                position: relative;
                img{
                        max-width: 100%;
                }
            }
            h3{
                font-size: 20px;
                font-weight: 500;
                line-height: 29px;
                margin-top: 7px;
                margin-bottom: 17px;
                color: $navyBlue;
                display: inline-block;
                @include media('>=desktop'){
                    padding-top: 0px;
                    font-size: 20px;
                    word-spacing: 1px;
                }
                @include media('>=large-desktop'){
                    font-size: 25px;
                    line-height: 35px;
                    padding-top: 5px;

                }
            }
            span{
                display: inline-block;
                position: relative;
                margin-left: 50px;
                margin-bottom: 5px;
                background: $orange;
                width: 30px;
                height: 2px;
                &:before{
                    content: "";
                    position: absolute;
                    left: 40px;
                    top: -1px;
                    width: 5px;
                    height: 5px;
                    background: $orange;
                    border-radius: 50%;
                }
            }
            p{
                    @include media('>=large-desktop'){    
                        padding-top: 15px;
                    }
                }
            }
            .button{
                width: 100%;
                min-height: 60px;
                background: $azure;
                text-align: left;
                text-transform: uppercase;
                margin: 0px;
                bottom: 0px;
                font-size: 18px;
                padding: 14px 18px;
                @include media('>=desktop'){
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 1px;
                    line-height: 20px;
                    padding: 18px 30px;
                }
                @include media('>=large-desktop'){
                    height: 70px;
                    font-size: 18px;
                    font-weight: 500;
                    letter-spacing: 1px;
                    padding-left: 42px;
                    padding-top: 25px;
                }
                span{
                    float: right;
                    border-radius: 10px;
                }
            }
        }
        @include media('>=desktop'){
            .item:nth-child(1) .item_content h3{
                &:before{
                    right: 43px;
                }
                &:after{
                    right: 30px;
                }
            }
        }
        @include media('>=large-desktop'){
            .item:nth-child(1) .item_content h3{
                &:before{
                    right: 55px;
                }
                &:after{
                    right: 43px;
                }
            }
        }
        }
}
}