.we_serve{
    background-color: $blue;
    padding: 50px 0;
    @include media('>=desktop'){
        background-image: unset!important;
        padding-bottom: 340px; 
    }
    @include media('>=large-desktop'){
        padding-bottom: 365px;
    }

    .what_we_serve{
               h1{
                    font-family: $avenir;
                    font-size: 32px;
                    font-weight: 500;
                    line-height: 42px;
                    color: white;
                    margin-bottom: 25px;
                    @include media('>=desktop'){
                        font-size: 40px;
                        font-weight: 500;
                        line-height: 50px;
                    }
                    @include media('>=large-desktop'){
                        font-size: 60px;
                        font-weight: 500;
                        line-height: 70px;
                    }
               }
            p{
                font-family: $avenir;;
                font-size: 16px;
                font-weight: 300;
                line-height: 26px;
                color: white;
                padding-top: 3px;
                @include media('>=desktop'){
                    font-size: 16px;
                    font-weight: 300;
                    line-height: 26px;

                }
                @include media('>=large-desktop'){
                    font-size: 20px;
                    font-weight: 300;
                    line-height: 30px;
                    padding-top: 30px;
                }
            }

            a{
                font-family: $avenir;
                font-size: 16px;
                font-weight: 500;
                letter-spacing: 1px;
                line-height: 20px;
                text-transform: uppercase;
                color: white;
                @include media('>=large-desktop'){
                    font-size: 18px;
                    font-weight: 500;
                    letter-spacing: 1px;
                }
                span{
                    content: "";
                    background-image: url(/assets/images/RectangleFooter.png);
                    background-repeat: no-repeat;
                    float: left;
                    width: 24px;
                    color: $azure;
                    height: 16px;
                    margin-top: 18px;
                    padding-left: 34px;
                    @include media('>=desktop'){
                        margin-top: 24px;
                    }
                    @include media('>=large-desktop'){
                        margin-top: 42px;
                    }
                }
            }
        }
}