.news_room_section{
    background: $cloud;
    padding: 50px 0;
    @include media('>=desktop'){
        margin-top: -120px;
        padding-top: 140px;
    }
    @include media('>=large-desktop'){
        margin-top: -100px;
        padding-top: 102px;
        padding-bottom: 140px;
    }
  
    .news_room{

               h1{
                font-family: $avenir;
                font-size: 32px;
                font-weight: 500;
                line-height: 42px;
                color:$navyBlue;
                @include media('>=desktop'){
                    font-size: 40px;
                    font-weight: 500;
                    line-height: 50px;

                }
                @include media('>=large-desktop'){
                    font-size: 60px;
                    font-weight: 500;
                    line-height: 70px;
                }
    }
        }
        .news_room_picture{
            min-width: 290px;
            width: 100%;
            height: 200px;
            background-size: cover;
            @include media('>=desktop'){
                height: 350px;
            }
        }
        .news_room_info{
            padding-top: 25px;
            font-family: $avenir;
            font-size: 16px;
            font-weight: 300;
            line-height: 26px;
            color: $darkGrey;
            @include media('>=desktop'){
                padding: 25px 0;
                &:after{
                    content: "";
                    display: block;
                    clear: both;
                }
            }
            @include media('>=large-desktop'){
                font-size: 20px;
                font-weight: 300;
                line-height: 30px;
                padding-top: 30px;
            }
            .tips{
                font-family: $avenir;
                font-size: 24px;
                font-weight: 500;
                line-height: 34px;
                color: $navyBlue;
                @include media('>=desktop'){
                    width: 33.3%;
                    float: left;
                    font-size: 25px;
                    font-weight: 500;
                    line-height: 35px;
                }
                @include media('>=large-desktop'){
                    font-size: 30px;
                font-weight: 500;
                line-height: 40px;
                }
            }

        }
            a{
                color: $azure;
                font-family: $avenir;
                font-size: 16px;
                font-weight: 500;
                letter-spacing: 1px;
                line-height: 20px;
                text-transform: uppercase;
                @include media('>=large-desktop'){
                    font-size: 18px;
                    font-weight: 500;
                    letter-spacing: 1px;
                    line-height: 22px;
                    
                }
                span{
                    content: "";
                    background-image: url(/assets/images/Rectangle.png);
                    background-repeat: no-repeat;
                    float: left;
                    width: 24px;
                    height: 16px;
                    margin-top: 2px;
                    padding-left: 35px;
                    @include media('>=desktop'){
                        margin-top: 3px;
                    }
                }
            }
        
}