.header {
    background: white;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    z-index: 100;
    padding: 10px;
    @include media('>=desktop') {
        padding: 20px;
    }
}

.main_logo{
    @include media('>=desktop') {
        width: 10%;
    }
    a{
        
        img{
            max-width: 100%;
        }
    }
}
.menu{
    display: none;
    text-align: center;
    padding-top: 150px;
    @include media('>=desktop'){
        display: flex;
        align-items: center;
        flex-flow: row wrap;
        justify-content: flex-end;
        width: 70%;
        padding: 0;
    }
    .menu_list{
        @include media('>=desktop'){
            display: flex;
            flex-flow: row nowrap;
            align-items: center;
            position: relative;
        }
        li{
            margin-bottom: 10px;
            padding: 0;
            @include media('>=desktop'){
                margin-bottom: 0;
            }
            &:before{
                display: none;
            }
            a{
                display: inline-block;
                font-size: 18px;
                font-weight: 500;
                line-height: 20px;
                @include media('>=desktop'){
                    padding: 0 15px
                }
            }
            .toggle_dropdown{
                    &:before {
                        content: "";
                        transform: translate(15px, 9px) rotate(50deg);
                        position: absolute;
                        background-color: $azure;
                        width: 2px;
                        height: 10px;
                        border-radius: 3px;
                    }
                    &:after{
                        content: "";
                        position: absolute;
                        background-color: $azure;
                        transform: translate(15px, 3px) rotate(130deg);
                        width: 2px;
                        height: 10px;
                        border-radius: 3px;
                    }
                @include media('>=desktop') {
                    display: none;
                }
            }
            .rotate{
                transform: rotate(90deg);
                margin: -15px 4px;
                position: absolute;
                width: 20px;
                height: 20px;
            }
            ul{
                display: none;
                z-index: 1;
                position: relative;
                width: 190px;
                background: white;
                @include media('>=desktop'){
                    background: $navyBlue;
                    border-bottom: 4px solid $azure;
                    position: absolute;
                    top: 62px;
                    left: 0px;
                    width: 300px;
                    padding: 10px;
                    box-shadow: none;
                    text-align: left;
                }
                @include media('>=large-desktop'){
                    background: $navyBlue;
                    border-bottom: 4px solid $azure;
                    position: absolute;;
                    max-width: 350px;
                    box-shadow: none;
                }
                li {
                    a {
                        padding: 10px;
                        padding-bottom: 0px;
                        line-height: 20px;
                        display: inline-block;
                        color: white;
                      &:hover{
                          color: greenyellow;
                          &:before{
                            @include media('>=desktop'){
                                content: "";
                                background: greenyellow;
                                width: 5px;
                                height: 5px;
                                float: right;
                                border-radius: 10px;
                                margin-top: 10px;
                                margin-left: 10px; 
                            }
                          }
                      }
                    }
                  }
    
            }
        }
        
        
    }
    .contact_block{
        display: flex;
        flex-flow: column;
        @include media ('>=desktop'){
            flex-flow: row nowrap;
        }
        a {
            padding: 10px;
        }
    }
}
.nav_toggle{
    width: 40px;
    height: 30px;
    position: relative;
    &.active{
        position: absolute;
        top: 4%;
        right: 3%;
    }
    cursor: pointer;
        span:before,
        span:after {
        cursor: pointer;
        border-radius: 1px;
        height: 5px;
        width: 40px;
        background: $navyBlue;
        position: absolute;
        display: block;
        content: '';
        transition: all 300ms ease-in-out;
        }
        span:before {
        top: 6px;
        }
        span:after {
        bottom: 5px;
        }
        &.active span {
        background-color: transparent;
        &:before,
        &:after {
        top: 10px;
        }
        &:before {
        transform: rotate(45deg);
        }
        &:after {
        transform: rotate(-45deg);
        }
        }

        @include media('>=desktop') {
            display: none;
        }
   }

.header-active{
    flex-flow: column wrap;
    .main_logo{
        display: none;
    }
    .menu{
        display: block;
        min-height: 100vh;
    }
}






    



