
.testimonial{
    position: relative;
    background: $cloud;
    padding-bottom: 1px;
    @include media('>=desktop'){
        margin-top: 150px;
        padding-bottom: 90px;
    }
    .container{
       .slider{
           padding-top: 67px;
           @include media('>=desktop'){
            padding-top: 85px;
            width: 812px;
            margin: 0px auto;
            }
            @include media('>=large-desktop'){
                padding-top: 85px;
                width: 1250px;
            }


        .info-person{
            padding-bottom: 25px;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            font-family: "Avenir - Book";
            font-size: 18px;
            font-weight: 300;
            line-height: 28px;
            color: $darkGrey;
            @include media('>=desktop'){
                font-size: 20px;
                line-height: 30px;
                display: block;
                padding-top: 23px;
                padding-bottom: 0px;
                text-align: left;
                line-height: 30px;
            }
            @include media('>=large-desktop'){
                font-size: 25px;
                font-weight: 300;
                line-height: 35px;
                padding-top: 60px;
            }
            &:before{
                content: "";
                background-image: url(/assets/images/quote1.png);
                position: absolute;
                width: 30px;
                left: 0px;
                top: 0px;
                height: 20px;
                @include media('>=desktop'){
                    top:21px;
                }
                @include media('>=large-desktop'){
                    top: 65px;
                    width: 45px;
                    background-image: url(/assets/images/quoteLeftBig.png);
                    height: 30px;                
                }
            }
            &:after{
                content: "";
                background-image: url(/assets/images/quote2.png);
                position: absolute;
                width: 30px;
                right: 0px;
                top: 90%;
                height: 20px;
                @include media('>=desktop'){
                    top: 79%;
                }
                @include media('>=large-desktop'){
                    top: 89%;
                    background-image: url(/assets/images/quoteRightBig.png);
                    right: 0px;
                    width: 45px;
                    height: 30px;
                    background-repeat: no-repeat;
                }
            }

            .person_image{
                margin-top: 31px;
                margin-left: -13px;
                padding-bottom: 22px;
                @include media('>=desktop'){
                    float: left;
                    margin: 13px 55px 0px 56px;
                   padding-bottom: 0px;
                 }
                 @include media('>=large-desktop'){
                    margin: 5px 78px 0px 80px;
                 }
             img{
                border-radius: 50%;
                @include media('>=large-desktop'){
                    width: 210px;
                    height: 210px;
                }
             }
            }
                blockquote{
                    margin: 0 0 17px;
                    @include media('>=desktop'){
                        width: calc(100% - 58px);
                     }
                     @include media('>=large-desktop'){
                        padding-top: 30px;
                        margin: 0 0 30px;
                        width: calc(100% - 100px);
                     }
                }
                .authorName{
                    font-family: "Avenir Medium";
                    font-weight: 500;
                    text-transform: uppercase;
                    color: $navyBlue;
                    font-size: 16px;
                    margin-bottom: 0px;
                    @include media('>=desktop'){
                        margin: -4px;
                    }
                    @include media('>=large-desktop'){
                        font-size: 20px;
                        color: $navyBlue;
                    }
                }
                .authorTitle{
                    font-size: 16px;
                    @include media('>=large-desktop'){
                        font-size: 20px;
                    }
                }
        }
        
        .slick-dots{
            width: 95%;
            display: flex;
            flex-direction: row;
            justify-content: center;
            padding-top: 15px;
            margin-bottom: 50px;
            @include media('>=desktop'){
                margin: 6px 27.5px;
                width: 90%;
            }
            @include media('>=large-desktop'){
                margin: 48px 47px;
            }
            li{
                margin-left: 28px;
                padding-left: 0px;
                &:before{
                    display: none;
                } 
                button{
                    width: 0px;
                    height: 10px;
                    border-radius: 50px;
                    padding: 5px;
                    background: $navyBlue;
                    @include media('>=desktop'){
                        padding: 4px;
                    }
                    &:before{
                        content: "";
                        position: absolute;
                    }
                }
            } 
        }
        .slick-dots  .slick-active button {
            background: $azure!important;
            width: 6px; 
            height: 6px;
            padding: 4px;
            position: relative;
            &:before {
                content: "";
                width: 20px; 
                height: 20px;
                position: absolute; 
                left: calc(50% - 10px); 
                top: calc(50% - 10px);
                background: transparent;
                border: 2px solid $azure;
                border-radius: 50% 50% 50% 50%;
                box-shadow: 0 0 3px 1px rgba(0,0,0,.3);
            }
        }
       }
    }
}


