.we_offer{
    background-color: $blue;
    padding: 50px 0;
    @include media('>=desktop'){
        background-image: url(/assets/images/logo_element_dark.png)!important;
        background-repeat: no-repeat!important;
        background-position: 0% 62%!important;
        background-size: 49%!important;
    }
    
    @include media('>=large-desktop'){
        background-position: 0% 55%!important;
        background-size: 50%!important;
    }
    .container{
        .what_we_offer{
                h1{
                    font-family: $avenir;
                    font-size: 32px;
                    font-weight: 500;
                    line-height: 42px;
                    color: white;

            @include media('>=desktop'){
                font-size: 40px;
                font-weight: 500;
                line-height: 50px;
            }
            @include media('>=large-desktop'){
                font-size: 60px;
                font-weight: 500;
                line-height: 70px;
            }
        }
            }
            p{
                font-family: $avenir;
                font-size: 16px;
                font-weight: 300;
                line-height: 26px;
                color: $cloud;
                @include media('>=large-desktop'){
                    font-size: 20px;
                    font-weight: 300;
                    line-height: 30px;
                }      
            }
                .what_we_offer_list{
                    @include media('>=tablet'){
                        margin: 0px auto;
                        padding: 10px;
                        column-count: 3;
                    }
                    @include media('>=large-desktop'){
                        column-count: 5;
                    }
                    li{
                        margin-bottom: 15px;
                        display: inline-block;
                        @include media('>=large-desktop'){
                            margin-bottom: 18px;
                            font-weight: 300;
                            line-height: 30px;
                        }
                        &:before{
                            content: "";
                            width: 10px;
                            height: 10px;
                            border: 2px solid $orange;
                        }
                        a{
                            font-size: 16px;
                            font-weight: 300;
                            line-height: 26px;
                            color: $cloud;
                           
                            @include media('>=large-desktop'){
                                font-size: 20px;
                                font-weight: 300;
                                line-height: 30px;
                            }
                        }
                }
            }
        }
}
    

